import { AppStateInterface } from '@store/app/app-state.interface';
import { NewArtworkStateInterface } from '@store/newArtwork/newArtworkState.interface';
import { createSelector } from '@ngrx/store';

export const newArtworkFeatureSelector = (state: AppStateInterface): NewArtworkStateInterface => state.newArtwork;

export const selectIsLoadingNewArtwork = createSelector(newArtworkFeatureSelector, (state: NewArtworkStateInterface) => state.isLoading);
