import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { CommonModule } from '@angular/common';

import { aspectRatioList } from '@utils/aspectRatioList';
import { TranslateModule } from '@ngx-translate/core';
import { TooltipModule } from 'primeng/tooltip';

@Component({
  selector: 'app-aspect-ratio-controls',
  templateUrl: 'aspect-ratio-controls.component.html',
  styleUrls: ['aspect-ratio-controls.component.scss'],
  standalone: true,
  imports: [CommonModule, TranslateModule, TooltipModule],
})
export class AspectRatioControlsComponent implements OnChanges {
  @Input() aspectRatio!: number;
  @Output() aspectRatioChange: EventEmitter<number> = new EventEmitter<number>();

  aspectRatioOriginal: number = 1;
  isAspectRatioCorrect: boolean = true;
  readonly aspectRatioList = aspectRatioList;

  ngOnChanges(changes: SimpleChanges) {
    if (changes['aspectRatio'] && !changes['aspectRatio'].firstChange) {
      this.aspectRatioOriginal = this.aspectRatio;
      this.isAspectRatioCorrect = this.aspectRatio >= 9 / 16 && this.aspectRatio <= 16 / 9;
    }
  }

  changeAspectRatio(value: number) {
    this.aspectRatio = value;
    this.aspectRatioChange.emit(value);
  }
}
