import { Injectable } from '@angular/core';
import { Artist } from '@ApiClient/backend/Artist/artist';

export interface IArtistForInput {
  name: string;
  id: string;
}

@Injectable({
  providedIn: 'root',
})
export class ArtistNameService {
  async getArtistNames(): Promise<IArtistForInput[] | []> {
    await Artist.updateLastest();
    return Artist.items ? Artist.items.map((a) => ({ id: a.id, name: a.names[0] ?? '' })) : [];
  }
}
