import { DATE_FORMAT_ARTWORK } from '@enums/date-format';
import { DateFormatTypes } from '@ApiClient/backend/Types/dateFormatType';

export interface IDateFormatOption {
  name: string;
  code: DATE_FORMAT_ARTWORK;
}

type DateFormType = {
  year: Date | null;
  monthYear: Date | null;
  full: Date | null;
  century: string | null;
  decade: string | null;
  other: string | null;
};

export const dateFormatOptions: IDateFormatOption[] = [
  {
    name: 'Century',
    code: DATE_FORMAT_ARTWORK.CENTURY,
  },
  {
    name: 'Decade',
    code: DATE_FORMAT_ARTWORK.DECADE,
  },
  {
    name: 'Year',
    code: DATE_FORMAT_ARTWORK.YEAR,
  },
  {
    name: 'MM/YY',
    code: DATE_FORMAT_ARTWORK.MONTH_YEAR,
  },
  {
    name: 'Full date',
    code: DATE_FORMAT_ARTWORK.FULL_DATE,
  },
  {
    name: 'Other',
    code: DATE_FORMAT_ARTWORK.OTHER,
  },
];

export function getNewArtworkDate(format: DATE_FORMAT_ARTWORK, date: DateFormType): string {
  switch (format) {
    case DATE_FORMAT_ARTWORK.YEAR:
      return date.year ? date.year?.getFullYear().toString() : '';
    case DATE_FORMAT_ARTWORK.MONTH_YEAR: {
      if (date.monthYear) {
        const month = date.monthYear?.getMonth();
        const year = date.monthYear?.getFullYear();
        return `${month + 1}/${year}`;
      } else {
        return '';
      }
    }
    case DATE_FORMAT_ARTWORK.FULL_DATE: {
      if (date.full) {
        const day = date.full?.getDate();
        const month = date.full?.getMonth();
        const year = date.full?.getFullYear();
        return `${day}/${month + 1}/${year}`;
      } else {
        return '';
      }
    }
    case DATE_FORMAT_ARTWORK.DECADE:
      return date.decade ? `${date.decade}s` : '';
    case DATE_FORMAT_ARTWORK.CENTURY:
      return date.century ?? '';
    default:
      return date.other ?? '';
  }
}

export function getDateFormat(format: DATE_FORMAT_ARTWORK, date: string): keyof typeof DateFormatTypes {
  if (!date) {
    return 'Unknown';
  } else {
    switch (format) {
      case DATE_FORMAT_ARTWORK.CENTURY:
        return 'Roman';
      case DATE_FORMAT_ARTWORK.DECADE:
      case DATE_FORMAT_ARTWORK.YEAR:
      case DATE_FORMAT_ARTWORK.FULL_DATE:
      case DATE_FORMAT_ARTWORK.MONTH_YEAR:
        return 'Date';
      default:
        return 'Unknown';
    }
  }
}
