import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import {
  AbstractControl,
  AsyncValidatorFn,
  FormArray,
  FormBuilder,
  FormControl,
  FormGroup,
  ValidationErrors,
  Validators,
} from '@angular/forms';
import { map } from 'rxjs/operators';
import { Observable, startWith } from 'rxjs';
import { MessageService } from 'primeng/api';
import { AutoCompleteCompleteEvent } from 'primeng/autocomplete';
import { Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';

import Artist from '@ApiClient/services/artist';
import { IArtworkData } from '@ApiClient/backend/Post/artwork';
import { LangCode } from '@ApiClient/backend/Common/language';

import { URLS } from '@enums/urls';
import { DATE_FORMAT_ARTWORK } from '@enums/date-format';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectCurrentUserNickname, selectCurrentUserProfileName } from '@store/auth/auth.selectors';
import { selectIsLoadingNewArtwork } from '@store/newArtwork/newArtwork.selectors';
import { postNewArtworkAction } from '@store/newArtwork/newArwork.actions';
import { ArtistNameService, IArtistForInput } from '@services/artist-name.service';
import { filterArtistNames } from '@utils/filterArtistNames';
import { dateFormatOptions, getDateFormat, getNewArtworkDate } from '@utils/dateFormatList';

@Component({
  selector: 'app-new-artwork',
  templateUrl: 'new-artwork.component.html',
  styleUrls: ['new-artwork.component.scss'],
})
export class NewArtworkComponent implements OnInit {
  @ViewChild('fileInput') fileInput!: ElementRef;
  maxDate!: Date;
  isDragover: boolean = false;
  originalAspectRatio: number = 1;
  templateAspectRatio: number = 1;
  fileToCropper: File | null = null;
  originalFile: string | null = null;
  originalFileExt: string = 'png';
  thumbnailFile: string | null = null;
  maxLengthDescription: number = 5000;
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingNewArtwork);
  userName$: Observable<string | null> = this.store.select(selectCurrentUserProfileName);
  currentDateFormat: DATE_FORMAT_ARTWORK = DATE_FORMAT_ARTWORK.FULL_DATE;
  locale$: Observable<string> = this.translate.onLangChange.pipe(
    startWith({ lang: this.translate.currentLang }),
    map((event) => event.lang),
  );
  currentUserNickname$: Observable<string | null> = this.store.select(selectCurrentUserNickname);

  artistsList: IArtistForInput[] = [];
  filteredArtistsList: IArtistForInput[] = [];

  constructor(
    private store: Store<AppStateInterface>,
    private fb: FormBuilder,
    private toast: MessageService,
    private artistNameService: ArtistNameService,
    private translate: TranslateService,
  ) {}

  newArtworkForm = this.fb.group({
    artists: this.fb.array([new FormControl('')]),
    title: new FormControl('', [Validators.required]),
    description: new FormControl('', [Validators.required, Validators.maxLength(this.maxLengthDescription)]),
    additional: new FormGroup({
      technique: new FormControl(''),
      dimensions: new FormControl(''),
      material: new FormControl(''),
      others: new FormControl(''),
      // preservation: new FormControl(''),
      // location: new FormControl(''),
    }),
    date: new FormGroup({
      year: new FormControl(''),
      monthYear: new FormControl(''),
      full: new FormControl(''),
      century: new FormControl('', [Validators.pattern(/^M{0,4}(CM|CD|D?C{0,3})(XC|XL|L?X{0,3})(IX|IV|V?I{0,3})$/)]),
      decade: new FormControl('', [Validators.pattern(/^(\d{0,3}0)$/)]),
      other: new FormControl(''),
      beforeChrist: new FormControl(false),
    }),
    isArtistUser: new FormControl(false, [Validators.required]),
    agreeToTerms: new FormControl(false, [Validators.requiredTrue]),
    // price: new FormControl(0),
  });

  get artists() {
    return this.newArtworkForm.controls['artists'] as FormArray;
  }

  get date() {
    return this.newArtworkForm.controls['date'] as FormGroup;
  }

  get additional() {
    return this.newArtworkForm.controls['additional'] as FormGroup;
  }

  ngOnInit() {
    this.artistNameService.getArtistNames().then((artists) => {
      this.artistsList = artists;
    });

    this.maxDate = new Date();

    this.newArtworkForm.get('isArtistUser')?.valueChanges.subscribe((value) => {
      if (value) {
        this.userName$.subscribe((username) => {
          if (username) {
            this.artists.controls[0].setValue(username);
            this.artists.controls[0].disable();
          }
        });
      } else {
        this.artists.controls[0].setValue('');
        this.artists.controls[0].enable();
      }
    });
  }

  clickFileInput() {
    this.fileToCropper = null;
    this.fileInput.nativeElement.click();
  }

  changeDateFormat(format: number) {
    this.currentDateFormat = format;
    this.date.reset();
  }

  onSubmit() {
    if (this.newArtworkForm.invalid) {
      this.newArtworkForm.markAllAsTouched();
      return;
    }

    const artistNames: string[] = filterArtistNames(this.artists.getRawValue(), this.newArtworkForm.controls.isArtistUser.value ?? false);
    const dateValue: string = getNewArtworkDate(this.currentDateFormat, this.date.value);
    const newArtwork: IArtworkData = {
      post: {
        files: [
          {
            fileExt: this.originalFileExt,
            dataBase64: this.originalFile ?? '',
            thumbnail: {
              fileExt: 'png',
              dataBase64: this.thumbnailFile ?? '',
            },
          },
        ],
        languages: [
          {
            title: this.newArtworkForm.get('title')?.value ?? '',
            description: this.newArtworkForm.get('description')?.value ?? '',
            langCode: this.translate.currentLang as keyof typeof LangCode,
          },
        ],
      },
      languages: [
        {
          ...this.additional.value,
          langCode: this.translate.currentLang as keyof typeof LangCode,
        },
      ],
      date: getNewArtworkDate(this.currentDateFormat, this.date.value),
      dateFormatType: getDateFormat(this.currentDateFormat, dateValue),
      afterChrist: !this.date.get('beforeChrist')?.value,
      isArtistUser: this.newArtworkForm.get('isArtistUser')?.value as boolean,
    };

    this.store.dispatch(postNewArtworkAction({ newArtwork, artistNames }));
  }

  async onSelectCroppedFile(file: string) {
    this.thumbnailFile = file;
  }

  addArtist() {
    this.artists.push(this.fb.control('', [Validators.required], [this.artistNameValidator()]));
  }

  deleteArtist(artistIndex: number) {
    this.artists.removeAt(artistIndex);
  }

  filterAutoCompleteArtists(event: AutoCompleteCompleteEvent) {
    const query = event.query.toLowerCase();
    this.filteredArtistsList = this.artistsList.filter((artist) => {
      const artistNameWords = artist.name.toLowerCase().split(' ');
      return artistNameWords.some((word) => word.startsWith(query));
    });
  }

  async onSelectFileUpload(event: Event) {
    const target = event.target as HTMLInputElement;
    const fileList: FileList | null = target.files;
    if (fileList && fileList.length > 0) {
      const file = fileList[0];
      if (!file.type.startsWith('image/')) {
        this.showErrorMessage('ERROR.WRONG_TYPE');
      } else {
        this.originalFileExt = file.type.split('/')[1];
        const reader = new FileReader();
        reader.onload = (event) => {
          const img = new Image();
          this.originalFile = reader.result as string;
          img.onload = () => {
            this.originalAspectRatio = img.width / img.height;
            if (this.originalAspectRatio > 16 / 9) {
              this.templateAspectRatio = 16 / 9;
            } else if (this.originalAspectRatio < 9 / 16) {
              this.templateAspectRatio = 9 / 16;
            } else {
              this.templateAspectRatio = img.width / img.height;
            }
          };
          img.src = event.target?.result as string;
        };
        reader.readAsDataURL(file);
        this.fileToCropper = file;
      }
    }
  }

  async storeFile($event: Event) {
    this.isDragover = false;
    this.fileToCropper = ($event as DragEvent).dataTransfer
      ? ($event as DragEvent).dataTransfer?.files.item(0) ?? null
      : ($event.target as HTMLInputElement).files?.item(0) ?? null;
  }

  private showErrorMessage(key: string): void {
    this.toast.add({
      severity: 'error',
      summary: this.translate.instant('ERROR.TITLE'),
      detail: this.translate.instant(key),
    });
  }

  changeAspectRatio(ratio: number) {
    this.templateAspectRatio = ratio;
  }

  private artistNameValidator(): AsyncValidatorFn {
    return (control: AbstractControl): Observable<ValidationErrors | null> | Promise<ValidationErrors | null> => {
      return Artist.search(control.value).pipe(
        map((artists) => {
          if (artists.length > 0) {
            return null;
          } else {
            return { artistNotExist: true };
          }
        }),
      );
    };
  }

  protected readonly URLS = URLS;
  protected readonly DATE_FORMAT_ARTWORK = DATE_FORMAT_ARTWORK;
  protected readonly dateFormatOptions = dateFormatOptions;
}
